import * as React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { NavLink } from 'reactstrap';
import './Home.scss';

class Home extends React.PureComponent {
    public render() {
        const getProcessoptimization = () => {
            return (
                <NavLink tag={Link} to="/processes/optimization">
                    <Card className="process-optimization">
                        <CardBody>
                            <CardTitle>Process optimization</CardTitle>
                            <CardText>
                                Optimize your processes
                            </CardText>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        const getTraceability = () => {
            return (
                <NavLink tag={Link} to="/traceability/task">
                    <Card className="traceability">
                        <CardBody>
                            <CardTitle>Traceability</CardTitle>
                            <CardText>
                                Access station data remotely
                            </CardText>
                        </CardBody>
                    </Card>
                </NavLink>
            );
        }

        return (
            <div className="content-home">
                <Row xs={1} sm={2}>
                    <Col>{getProcessoptimization()}</Col>
                    <Col>{getTraceability()}</Col>
                </Row>
            </div>
        );
    }
};

export default Home;
