import Home from './components/content-pages/Home';
import ProcessOptimization from './components/content-pages/ProcessOptimization';
import Stations from './components/content-pages/Stations';
import Tasks from './components/content-pages/Tasks';
import TaskNew from './components/content-pages/TaskNew';
import TaskEdit from './components/content-pages/TaskEdit';
import TaskDetail from './components/content-pages/TaskDetail';
import TaskHistoryDetail from './components/content-pages/TaskHistoryDetail';
import Work from './components/content-pages/Work';

export default [
  { path: "/", name: "Home", Component: Home },
  { path: "/processes/optimization", name: "Processes", Component: ProcessOptimization },
  { path: "/traceability/stations", name: "Stations", Component: Stations },
  { path: "/traceability/task", name: "Tasks", Component: Tasks },
  { path: "/traceability/task/new", name: "New", Component: TaskNew },
  { path: "/traceability/task/:id/detail", name: "Details", Component: TaskDetail },
  { path: "/traceability/task/:id/edit", name: "Edit", Component: TaskEdit },
  { path: "/traceability/task/:taskId/history/:historyId", name: "History", Component: TaskHistoryDetail },
  { path: "/traceability/work", name: "Work", Component: Work }
];
